
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  width:100%;
  overflow: scroll;
}
a{
  text-decoration: none;
}
.btn{
    position: fixed;
    top: 30px;
    right: 15px;
    width: 40px;
    height: 40px;
    padding:0;
    border: 0;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 3px;
    outline: none;
    font-weight: bold;
}
.iconfont{
    color: #fff;
    height:100%;
    width:100%;
    font-size: 17px;
}
.blank{
  display: none;
  height:100%;
  width:100%;
  background: rgba(0,0,0,.5);
  position: absolute;
  top:0;
  left:0;
  z-index: 1000;
}
.blank.active{
  display: block;
}










